import React, { useState, useContext } from 'react';
import { NavLink, Link, useHistory } from "react-router-dom";
import Nav from 'react-bootstrap/Nav'
import { Navbar, NavDropdown, Image, Form, Modal } from 'react-bootstrap/'

import img1 from '../ms-ribbon/icons/normal/new-table.png'
import img2 from '../ms-ribbon/icons/hot/new-table.png'
import img3 from '../ms-ribbon/icons/disabled/new-table.png'
import img4 from '../ms-ribbon/icons/normal/open-table.png'
import img5 from '../ms-ribbon/icons/hot/open-table.png'
import img6 from '../ms-ribbon/icons/normal/open-table.png'
import img7 from '../ms-ribbon/icons/disabled/delete-table.png'
import img8 from '../ms-ribbon/icons/hot/delete-table.png'
import img9 from '../ms-ribbon/icons/disabled/delete-table.png'
import img10 from '../ms-ribbon/icons/normal/new-page.png'
import img11 from '../ms-ribbon/icons/hot/new-page.png'
import img12 from '../ms-ribbon/icons/disabled/new-page.png'
import img13 from '../ms-ribbon/icons/normal/open-page.png'
import img14 from '../ms-ribbon/icons/hot/open-page.png'
import img15 from '../ms-ribbon/icons/disabled/open-page.png'
import img16 from '../ms-ribbon/icons/normal/delete-page.png'
import img17 from '../ms-ribbon/icons/hot/delete-page.png'
import img18 from '../ms-ribbon/icons/disabled/delete-page.png'

import img19 from '../ms-ribbon/icons/normal/run.png'
import img20 from '../ms-ribbon/icons/hot/run.png'
import img21 from '../ms-ribbon/icons/disabled/run.png'

import img22 from '../ms-ribbon/icons/normal/repeat.png'
import img23 from '../ms-ribbon/icons/hot/repeat.png'
import img24 from '../ms-ribbon/icons/disabled/repeat.png'

import img25 from '../ms-ribbon/icons/normal/save.png'
import img26 from '../ms-ribbon/icons/hot/save.png'
import img27 from '../ms-ribbon/icons/disabled/save.png'

import img28 from '../ms-ribbon/icons/normal/bullet-orange.png'
import img29 from '../assets/gps-location-disabled.png'

import '../ms-ribbon/css/ribbon.css';

import BSOD from '../assets/BSOD.wav'


var config = require('../config.json')

var Header = () => {


  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);



  function crash(){  

    let root = document.documentElement;    
    var crashaudio = new Audio(BSOD)

    crashaudio.play();  

    setTimeout(() => {
      root.style.setProperty('--err-display', 'visible');
    }, 900);

      
 
}


  return (
    <>
   <Modal show={show} onHide={handleClose} animation={false}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
        <Modal.Footer>   
     
        </Modal.Footer>
      </Modal>
  <label for="ribbon_middle_menu_toggle" unselectable="on" class="ms-ribbon-toggle ms-window-title">
	<div class="ribbon-menu">
		<span class="line"></span>
		<span class="line"></span>
		<span class="line"></span>
	</div>
	<span class="title">
		dash-go.xlsx
	</span>
</label>
<input type="radio" checked id="ribbon_middle_menu_toggle" class="ms-ribbon-radio" />
<div class="ms-ribbon">
	<ul>
		<li class="file">
			<span class="tab">File</span>
			<div id="tab-content0" class="ribbon-content"></div>
		</li>
		<li>
			<input type="radio" checked name="tabs" id="tab1" class="ribbon-hidden"/>
			<label for="tab1" class="tab">Navigation</label>
  
			<div id="tab-content1" class="ribbon-content">


				<div class="ribbon-section" unselectable="on">
					<span class="section-title" unselectable="on">Tables</span>

					<div class="ribbon-button ribbon-button-large" id="add-table-btn" unselectable="on">

						<span class="button-help"
						      unselectable="on">This button will add a table to your document.</span>
            <NavLink to="/all-time" activeClassName="selected" id="alltime">
						<img class="ribbon-icon ribbon-normal" src={img1} />
						<img class="ribbon-icon ribbon-hot" src={img2} />
						<img class="ribbon-icon ribbon-disabled" src={img3} /></NavLink>
						<span class="button-title" unselectable="on"><NavLink to="/all-time" activeClassName="selected" id="alltime">Totals</NavLink></span></div>
					<div class="ribbon-button ribbon-button-large" id="open-table-btn" unselectable="on">

					<span class="button-help" unselectable="on">This button will open a table and add it to your document.</span>
          <NavLink to="/weekly" activeClassName="selected" id="weekly">
						<img class="ribbon-icon ribbon-normal" src={img4} />
						<img class="ribbon-icon ribbon-hot" src={img5} />
						<img class="ribbon-icon ribbon-disabled" src={img6} /></NavLink>
						<span class="button-title"><NavLink to="/weekly" activeClassName="selected" id="weekly">Weekly</NavLink></span></div>

            <div class="ribbon-button ribbon-button-large" id="add-page-btn" unselectable="on">

						<span class="button-help" unselectable="on">This button will add a page to your document.</span>
            <NavLink to="/individual" activeClassName="selected" id="individual">
						<img class="ribbon-icon ribbon-normal" src={img10} />
						<img class="ribbon-icon ribbon-hot" src={img11} />
						<img class="ribbon-icon ribbon-disabled" src={img12} /></NavLink>
						<span class="button-title"><NavLink to="/individual" activeClassName="selected" id="individual">Individual</NavLink></span></div>

			

				</div>
				<div class="ribbon-section-sep" unselectable="on"></div>

				<div class="ribbon-section" unselectable="on">
					<span class="section-title" unselectable="on">Pages</span>

          <div class="ribbon-button ribbon-button-large disabled" id="del-table-btn">          
						<span class="button-help" unselectable="on">This button will remove the selected table from your document.</span>
						<img class="ribbon-icon ribbon-normal" src={img7} />
						<img class="ribbon-icon ribbon-hot" src={img8} />
						<img class="ribbon-icon ribbon-disabled" src={img9} />
						<span class="button-title" unselectable="on">Remove Table</span></div>
					
					<div class="ribbon-button ribbon-button-large disabled" id="open-page-btn" unselectable="on">
						<span class="button-help" unselectable="on">This button will open a page and add it to your document.</span>
						<img class="ribbon-icon ribbon-normal" src={img13} />
						<img class="ribbon-icon ribbon-hot" src={img14} />
						<img class="ribbon-icon ribbon-disabled" src={img15} />
						<span class="button-title" unselectable="on">Open Page</span></div>
					<div class="ribbon-button ribbon-button-large disabled" id="del-page-btn" unselectable="on">

						<span class="button-help" unselectable="on">This button will remove the selected page from your document.</span>
						<img class="ribbon-icon ribbon-normal" src={img16} />
						<img class="ribbon-icon ribbon-hot" src={img17} />
						<img class="ribbon-icon ribbon-disabled" src={img18} />
						<span class="button-title" unselectable="on">Remove Page</span></div>
				</div>
				<div class="ribbon-section-sep" unselectable="on"></div>


				<div class="ribbon-section" unselectable="on">
					<span class="section-title" unselectable="on">Actions</span>

					<div class="ribbon-button ribbon-button-small disabled" id="run-btn" unselectable="on">
						<span class="button-help" unselectable="on">This button will run the program.</span>
						<img class="ribbon-icon ribbon-normal"src={img21} />
						<img class="ribbon-icon ribbon-hot" src={img21} />
						<img class="ribbon-icon ribbon-disabled" src={img21} />
						<span class="button-title" unselectable="on">Run</span></div>

					<div class="ribbon-button ribbon-button-small disabled" id="repeat-btn" unselectable="on">
						<span class="button-help" unselectable="on">This button will repeat something.</span>
						<img class="ribbon-icon ribbon-normal" src={img22} />
						<img class="ribbon-icon ribbon-hot" src={img23} />
						<img class="ribbon-icon ribbon-disabled" src={img24} />
						<span class="button-title" unselectable="on">Repeat</span></div>

					<div class="ribbon-button ribbon-button-small disabled" id="save-btn" unselectable="on">
						<span class="button-help" unselectable="on">This button will save your document.</span>
						<img class="ribbon-icon ribbon-normal" src={img25} />
						<img class="ribbon-icon ribbon-hot" src={img26} />
						<img class="ribbon-icon ribbon-disabled" src={img27} />
						<span class="button-title" unselectable="on">Save</span></div>
				</div>
				<div class="ribbon-section-sep" unselectable="on"></div>

			</div>
		</li>


    <li>
			<input type="radio" name="tabs" id="tab2" class="ribbon-hidden"/>
			<label for="tab2" class="tab">Enhancers</label>

			<div id="tab-content2" class="ribbon-content">

				<div class="ribbon-section" unselectable="on">
					<span class="section-title" unselectable="on">More Stuff</span>

					<div class="ribbon-button ribbon-button-large disabled" unselectable="on" >
						<span class="button-help" unselectable="on">This button will do something else.</span>
						<img class="ribbon-icon ribbon-normal ribbon-hot ribbon-disabled ribbon-implicit-disabled"
						     src={img28} />
						<span class="button-title" unselectable="on">Disable aimbot</span></div>

					<div class="ribbon-button ribbon-button-large" id="other-btn-2" unselectable="on" onClick={() => crash()}>
						<span class="button-help" unselectable="on">This button will remove the selected table from your document.</span>
						<img class="ribbon-icon ribbon-normal ribbon-hot ribbon-disabled ribbon-implicit-disabled" width="32px" height="32px"
						     src={img29}/>
						<span class="button-title" unselectable="on">watkinsaj GPS</span>
					</div>
				</div>
				<div class="ribbon-section-sep" unselectable="on"></div>
			</div>
		</li>
		



	
	</ul>
</div><br/><br/><br/><br/>

  </>

  );
}

export default Header;